<template>
  <div>
    <div style="padding:10px 0;">
          <!-- 搜索与添加区域 -->
       <el-row gutter="20">
         <el-col :span="8">
           <el-input placeholder="请输入内容" clearable
           @clear="getBookList">
             <el-button slot="append" icon="el-icon-search" ></el-button>
          </el-input>
          </el-col>
       </el-row>
      <aplayer autoplay='false' :music="song" :list="musiclist" :v-if="flag"></aplayer>
    </div>
  </div>
</template>

<script>
import aplayer from 'vue-aplayer'

export default {
  name: 'Aplayer',
  components: {
    // 别忘了引入组件
    aplayer: aplayer
  },
  data () {
    return {
      flag: false,
      musiclist: [],
      song: { title: '', artist: '', src: '' }
    }
  },
  async mounted () {
    // 异步加载，先加载出player再使用
    this.init()
    const aplayer = this.$refs.player.control
    aplayer.play()
  },
  methods: {
    async init () {
      const { data: res } = await this.axios.post('/getstar', {
        username: window.sessionStorage.getItem('username')
      })
      this.musiclist = []
      if (res.status !== '1') {
        return this.$message.error(res.message)
      }
      if (res.status === '1') {
        this.$message.success(res.message)
        this.temp = res.songlists

        this.song.title = this.temp[0].title
        this.song.artist = window.sessionStorage.getItem('username')
        this.song.src = 'http://localhost:8900/static/' + this.temp[0].title

        this.temp.forEach(element => {
          const obj = {
            title: element.title,
            src: 'http://localhost:8900/static/' + element.title,
            artist: window.sessionStorage.getItem('username')
          }
          if (element.isstar === '1') {
            obj.isstar = '已收藏'
          } else {
            obj.isstar = '未收藏'
          }
          this.musiclist.push(obj)
        })
      };
    }
  }
}
</script>

<style lang="scss" scoped type="text/css">
</style>
